body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  width: 100%;
  height: 100vh;
  background-color: rgb(52, 18, 92);
  display: flex;
  justify-content: center;
  align-items: center;
}

.MainPage_container__brLBH {
  width: 60%;
  height: 80vh;
  border: 20px dashed aliceblue;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: purple;
}

.MainPage_container__brLBH h1 {
  font-size: 45px;
  font-family: 'Hanalei', cursive;
  letter-spacing: .5rem;
  text-transform: uppercase;
  color: aliceblue;
}
.MainPage_category__2i3yt , 
.MainPage_level__2ryM3 {
  width: 500px;
  display: flex;
}
.MainPage_category__2i3yt label , 
.MainPage_level__2ryM3 label {
  font-size: 20px;
  font-family: 'Lobster', cursive;
  letter-spacing: .2rem;
  color: aliceblue;
  margin-right: 50px;
}
.MainPage_category__2i3yt select ,
.MainPage_level__2ryM3 select {
  width: 200px;
  height: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 5px solid aliceblue;
  color: aliceblue;
  font-family: 'Cinzel', serif;
  cursor: pointer;
}
.MainPage_category__2i3yt select option,
.MainPage_level__2ryM3 select option {
  background-color: black;
}
.MainPage_level__2ryM3 select:hover,
.MainPage_category__2i3yt select:hover {
  background-color: black;
}

.MainPage_button__3xOQ4 {
  font-size: 15px;
  padding: 15px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  border: 5px dashed white;
  color: aliceblue;
  font-family: 'Pacifico', cursive;
  letter-spacing: .2rem;
}
.MainPage_button__3xOQ4:active {
  border: 5px dashed white;
}
.MainPage_button__3xOQ4:hover {
  background-color: rgb(17, 5, 126);
}
@media all and (max-width:950px) {
  
  .MainPage_category__2i3yt , 
  .MainPage_level__2ryM3 {
    flex-direction: column;
    text-align: center;
  }
  .MainPage_category__2i3yt label , 
  .MainPage_level__2ryM3 label {
    width: 200px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .MainPage_category__2i3yt select , 
  .MainPage_level__2ryM3 select {
    margin: 0 auto;
  }
  .MainPage_button__3xOQ4 {
    font-size: 10px;
    padding: 10px;
  }
}
@media all and (max-width:650px) {
  .MainPage_container__brLBH {
    width: 100%;
    height: 100vh;
  }
}
@media all and (max-width:500px) {
  .MainPage_category__2i3yt , 
  .MainPage_level__2ryM3 {
    width: 200px;
  }
  .MainPage_category__2i3yt select option,
.MainPage_level__2ryM3 select option {
  font-size: 8px;
}
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
section .MainPage_loader__2a03q {
  position: relative;
  width: 120px;
  height: 120px;
}
section .MainPage_loader__2a03q span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: rotate(calc(18deg * var(--i)));
}
section .MainPage_loader__2a03q span:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: greenyellow;
  box-shadow: 0 0 10px greenyellow , 
              0 0 20px greenyellow ,
              0 0 40px greenyellow ,
              0 0 60px greenyellow ,
              0 0 80px greenyellow ,
              0 0 100px greenyellow;
  animation: MainPage_animate__1KjhW 2s linear infinite;
  animation-delay: calc(0.1s * var(--i)) ;
}
@keyframes MainPage_animate__1KjhW {
  0% {
    transform: scale(1);
  }
  40%,100% {
    transform: scale(0);
  }
}
.Quiz_container__1LY4B {
  color: white;
  width: 50%;
  height: auto;
  border: 10px dashed white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: transparent;
  margin-bottom: 30px;
}
.Quiz_question__2EHtQ {
