}
.Quiz_new__3Ijn7 , 
.Quiz_reset__2Hj7c{
  width: 200px;
  font-size: 25px;
  padding: 15px;
  margin-bottom: 30px;
  cursor: pointer;
  font-family: 'Pangolin', cursive;
}
.Quiz_new__3Ijn7:hover , 
.Quiz_reset__2Hj7c:hover {
  opacity: .5;
}
.Quiz_flex__1cXUS { 
  display: flex;
  justify-content: space-around;
}
.Quiz_score__1xALL {
  font-size: 30px;
  margin-top: 20px;
